import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from 'src/app/core/auth/guards/auth.guard';

import { AssetsViewComponent } from './assets-view.component';


const routes: Routes = [
  { path: '', component: AssetsViewComponent, canActivate: [AuthGuard]},
  { path: ':id', component: AssetsViewComponent, canActivate: [AuthGuard] }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AssetsViewRoutingModule { }
